body, html, #root {
  min-height: 100vh;
  margin: 0;
  font-family: 'Segoe UI', Arial, sans-serif;
  background: linear-gradient(120deg, #ffe6e6 0%, #e6ffe6 100%);
}

.app-container {
  max-width: 420px;
  margin: 32px auto;
  padding: 20px;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.10);
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  margin-top: 0;
  color: #ff70a6;
}

.form-input {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.form-input input[type="text"], 
.form-input textarea {
  padding: 8px 10px;
  border-radius: 8px;
  border: 1px solid #eee;
  font-size: 1rem;
}

.form-input textarea {
  resize: vertical;
  min-height: 60px;
}

.form-input input[type="file"] {
  margin-top: 8px;
}

button {
  background: #ff70a6;
  color: #fff;
  border: none;
  padding: 8px 16px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.2s;
}

button:hover {
  background: #ff9770;
}

.birthday-card {
  width: 100%;
  background: #fff3cd;
  border-radius: 12px;
  border: 1px solid #ffeeba;
  padding: 20px 14px;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fadeIn 0.6s;
}

.birthday-card h2 span {
  color: #ff70a6;
}

.foto-ucapan {
  margin: 12px 0;
  max-width: 90%;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.12);
}

.music-controls {
  margin: 10px 0;
}

.share-actions {
  margin-top: 12px;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

footer {
  margin-top: 28px;
  font-size: 0.8rem;
  color: #888;
}

@media (max-width: 600px) {
  .app-container {
    max-width: 98vw;
    padding: 8vw 0;
  }
  .birthday-card {
    padding: 16px 4vw;
  }
}
@keyframes fadeIn {
  from { opacity: 0; transform: scale(0.96);}
  to { opacity: 1; transform: scale(1);}
}